:root{
	--main-bg-color: #FEF5ED;
    --container-bg-color: #F7ECDE;
    --primary-color-alt: #FF9F45;
    --main-font: 'Poppins', sans-serif;
}

html {
    font-size: 62.5%;
    box-sizing: border-box;
    scroll-behavior: smooth;
	background-color: var(--main-bg-color);
    font-family: var(--main-font);
}

*,
*:before,
*:after {
    box-sizing: inherit;
}
body {
    font-size: 1.6rem;
}

p {
    font-size: 2rem;
}
.container {
    width: 100%;
    margin: 0 auto;
    max-width: 2500px;
}

@media (min-width: 768px) {
    .container{
        width:95%
    }
}

img {
    max-width: 100%;
}
ul {
    text-decoration: none;
    margin: 0;
    padding: 0;
}
li {
    margin: 0;
}
a {
    text-decoration: none;
}

.subtitle{
    font-size: 3rem;
    text-align: center;
    text-decoration: underline;
}

/* Header */
.site-header{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
    justify-content: space-around;
}

.site-header img{
    align-self: center;
    width: 20rem;
    height: 20rem;
}

.logo{
    font-size: 3.5rem;
    text-align: center;
    display: flex;
    flex-direction: column-reverse;
}
.logo span{
    color: var(--primary-color-alt);
}
.logo img{
    display: inline-block;
}

.navbar{
    display: flex;
    flex-direction: column;

}
.navbar a{
    color: #000 ;
    margin: 2rem 0;
    font-size: 4rem;
    transition: all .3s ease-in-out;
}
.navbar a:hover{
    color: var(--primary-color-alt);
}

@media (min-width: 768px) {
    .site-header{
        flex-direction: row;
    }
    .logo{
        flex-direction: row-reverse;
        align-items: flex-end;
    }
    .logo img{
        height: 10rem;
        width: 10rem;
    }
    .navbar{
        flex-direction: row;
    }
    .navbar a{
        font-size: 2rem;
        margin-right: 2rem;
    }
}
/****************** Home Page *******************/
/* Why us section */
.whyus__container{
    background-color: var(--container-bg-color);
    display: flex;
    flex-direction: column;
    border-radius: 15px;
}

.whyus__img img{
    padding: 0 2rem;
    align-items: center;
}

.whyus__content{
    padding: 0 2rem;
}

@media (min-width: 768px) {
    .whyus__container{
        display: grid;
        grid-template-columns: 2fr 1fr;
    }
    .whyus__img img{
        padding: 2rem 2rem;
        height: 70rem;
    }
}
